.App-main {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.Clock-main {
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
    margin: 10vh 0 0 0;
    border-radius: 4px;
    /* padding: 2em; */
    background: #f3f5f5;
}

.Clock-main >div {
    padding: .5em;
}

.Clock-time {
    display: flex;
    justify-content: center;
    font-size: 4em;
    background: slategray;
    color: #ffffff;
}

.Clock-buttons > div > div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Clock-buttons > div > div button {
    flex: 1;
    margin: 1em 4px 0 4px;
    border: 1px solid transparent;
    padding: 8px;
    border-radius: 4px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    background: #e2ecec;
    position: relative;
    color: transparent;
    outline: none;
}

.Clock-buttons > div > div button:hover {
    background: #c3cece;
}

.Clock-buttons > div > div button.deactive {
    pointer-events: none;
    opacity: 0.5;
    /* color: #666; */
}

.Clock-buttons >div div button::after {
    position: absolute;
    left: 50%;
    top: -3px;
    font-size: 2em;
    transform: translate(-50%, 0);
    color: #000000;
}

button.reset::after {
    content: '\21ba';
    font-size: 1.5em;
    font-weight: 600;
}

button.pause::after {
    content: '';
    position: absolute;
    border-right: 3px solid #000;
    border-left: 3px solid #000;
    height: 20px;
    width: 5px;
    top: 6px !important;
}

button.start::after {
    content: '\25b6';
}

button.lap {
    color: #000 !important;
    font-size: 1.15em;
}

.lap-int {
    display: flex;
    margin: 14px;
}

.lap-int >div:first-of-type {
    border-right: 1px solid #000000;
    margin: 0 20px 0 0;
    padding: 0 10px;
}

.lap-int:nth-of-type(2n) {
    background: #e5e5e5;
}